body {
    font-family: sans-serif;
  }
  
  input {
    border: 1px solid #999;
    padding: 0.5rem;
    width: 300px;
  }
  

  
  .suggestions {
    z-index: 1 ;

    border: 1px solid #999;
    border-top-width: 0;
    list-style: none;
    margin-top: 0;
    max-height: 143px;
    overflow-y: auto;
    padding-left: 0;
    width: calc(300px + 1rem);
  }
  
  .suggestions li {
    padding: 0.5rem;
    background-color: white
  }
  
  .suggestion-active,
  .suggestions li:hover {
    background-color: gray;
    color: #fae042;
    cursor: pointer;
    font-weight: 700;
  }
  .suggestionsClass{
    position: absolute;
    z-index: 2 ;

  }
  .suggestions li:not(:last-of-type) {
    border-bottom: 1px solid #999;
  }
  .search{ 
    position: relative;
    display: flex;
    padding: 2%;
    z-index: 2 ;

  }
div.scrollmenu {
    background-color: #333;
    overflow-x:scroll;
    display: flex;
    padding: 5%;
    overflow:auto
}

.discountpercent{
    position: absolute;
    border-radius: 2px;
    top: 8px;
    right: 8px;
    background: #feefde;
    color: #f68b1e;
}
.sidefilter {
    width: 300px;
    position: fixed;

    background: #eee;
    overflow-x: hidden;
    padding: 8px 0;
  }
  @media screen and (max-width: 600px) {
    .sidefilter {
      width: 300px;
      position: relative;
      background: #eee;
      overflow-x: hidden;
      padding: 8px 0;
    }
  }
.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    padding-top: 60px;
  }
  
  .sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }
  
  .sidenav a:hover {
    color: #f1f1f1;
  }
  
  .sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
  }
  
  @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
  }
