body {
  margin: 0;
}

#navbar {
  overflow: hidden;
  background-color: #333;
  font-family: Arial, Helvetica, sans-serif;
}

#navbar a {
  float: left;
  font-size: 16px;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

#dropdown {
  float: left;
  overflow: hidden;
}

#dropdown #dropbtn {
  font-size: 16px;  
  border: none;
  outline: none;
  color: white;
  padding: 14px 16px;
  background-color: inherit;
  font: inherit;
  margin: 0;
}

#navbar a:hover, #dropdown:hover #dropbtn {
  background-color: red;
}

#dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  width: 100%;
  left: 0;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 3;
}

#dropdown-content #header {
  background: red;
  padding: 16px;
  color: white;
}

#dropdown:hover #dropdown-content {
  display: block;
}

/* Create three equal columns that floats next to each other */
#column {
  float: left;
  width: 33.33%;
  padding: 10px;
  background-color: #ccc;
}

#column a {
  float: none;
  color: black;
  padding: 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

#column a:hover {
  background-color: #ddd;
}

/* Clear floats after the columns */
#row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  #column {
    width: 100%;
    height: auto;
  }
}
